// 轮播图组件

import React,{Component} from "react";

import "./index.scss";

import { Carousel} from 'antd-mobile';


class Guide extends Component{
    constructor(){
         super();
        this.state={
            imgs: [
              require("../../../assets/images/one.jpg"),
              require("../../../assets/images/two.jpg"),
              require("../../../assets/images/three.jpg"),
              require("../../../assets/images/four.png"),
          ],
        }
    }

    toRead=()=>{
      console.log(this.props)
       
        this.props.history.push("/main/bookCity")
        
    }

    //  // 判断第几次访问这个页面，超过三次的话就直接进书城的页面
    componentDidMount(){
        if(localStorage.visitCount){
            localStorage.visitCount++;
            if(localStorage.visitCount>3){
             this.props.history.push("/main/bookCity")
            }

        }else{
            localStorage.visitCount=1;
        }
    }



    render(){
        return(
            <div className="container">
            <Carousel
            autoplay={false}
            infinite
            dots={false}
            beforeChange={(from, to) => console.log()}
            afterChange={index => console.log()}
            >
            {this.state.imgs.map((item,index) => (
                <a
                key={index}
                style={{ display: 'inline-block', width: '100%', height: "100vh" }}
                >

                {/* 一定要写一个src属性，不然图片出不来 */}
                <img
                    src={item}
                    style={{ width: '100%',height:"100vh", verticalAlign: 'top' }}
                    onLoad={() => {
                    window.dispatchEvent(new Event('resize'));
                    
                    }}
                />

                {/* 如果到了最后一个页面的轮播图，name点击进入按钮就可以进入到书城里面了 */}
                {
                    index===this.state.imgs.length-1&&<button className="toRead" onClick={this.toRead}>
                    开始阅读
                    </button>
                }
                </a>
            ))}
            </Carousel>
  
            </div>
        )
    }
}


export default Guide

